"use client";

import { useState } from 'react';

interface AnimatedButtonProps {
  children: React.ReactNode;
  hoverText: string;
  className?: string;
  onClick?: () => void;
}

export function AnimatedButton({ children, hoverText, className = "", onClick }: AnimatedButtonProps) {
  const [isHovered, setIsHovered] = useState(false);

  return (
    <button
      onClick={onClick}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      className={`
        relative overflow-hidden bg-white text-black px-8 py-2 rounded-2xl font-semibold 
        transition-all duration-500 ease-out cursor-pointer
        hover:bg-gray-200
        ${className}
      `}
    >
      {/* Black dot that fills the button */}
      <div 
        className={`
          absolute top-1/2 left-2 w-2 h-2 bg-black rounded-full
          transition-all duration-500 ease-out
          ${isHovered ? 'scale-[200]' : 'scale-100'}
        `}
        style={{
          transform: isHovered ? 'scale(200)' : 'scale(1)',
        }}
      />
      
      {/* Original text */}
      <span 
        className={`
          relative z-10 transition-all duration-500 ease-out
          ${isHovered ? 'opacity-0 translate-x-4' : 'opacity-100 translate-x-0'}
        `}
      >
        {children}
      </span>
      
      {/* Hover text */}
      <span 
        className={`
          absolute inset-0 flex items-center justify-center z-10
          transition-all duration-500 ease-out
          ${isHovered ? 'opacity-100 translate-x-0' : 'opacity-0 -translate-x-4'}
        `}
      >
        {hoverText}
      </span>
    </button>
  );
} 